.homepageContainer {
  width: 100vw;
  height: 100vh;
  display: flex;
  overflow: hidden;
  position: relative;
  .homepage__image-1 {
    height: 100%;
    transform: skew(30deg);
    margin-left: -1000px;
    width: calc(30vw + 1000px);
  }
  .homepage__image-2 {
    height: 100%;
    width: 40vw;
    background-color: black;
    transform: skew(30deg);
  }
}
